            <h4>{{ analysisResult.success ? 'ÂàÜÊûêÂÆåÊàêÔºÅ' : 'ÂàÜÊûêÂ§±Ë¥•' }}</h4>
            <p>{{ analysisResult.message }}</p>
            <div v-if="analysisResult.success && analysisResult.analysisResult" class="result-details">
              <p><strong>Â§ÑÁêÜËÄóÊó∂Ôºö</strong>{{ analysisResult.analysisResult.total_time.toFixed(2) }}Áßí</p>
              <p><strong>ÁîüÊàêÊñá‰ª∂Ôºö</strong>{{ analysisResult.analysisResult.file_check_results.generated_count }}/{{ analysisResult.analysisResult.file_check_results.total_count }}</p>
              <div v-if="analysisResult.analysisResult.generated_files.length > 0" class="generated-files">
                <h5>ÁîüÊàêÁöÑÊñá‰ª∂Ôºö</h5>
                <ul>
                  <li v-for="file in analysisResult.analysisResult.generated_files" :key="file.filename">
                    {{ file.filename }} ({{ (file.size / 1024).toFixed(1) }}KB)
                  </li>
                </ul>
