            </button>
            <button 
              v-if="uploadedFolderId && !analyzing && !analysisResult" 
              @click="startAnalysis" 
              class="btn btn-primary"
              :disabled="analyzing">
              <span class="btn-icon">üöÄ</span>
              {{ analyzing ? 'ÂàÜÊûê‰∏≠...' : 'ÂºÄÂßãÂàÜÊûê' }}
            </button>
            <button 
              v-if="uploadedFolderId || analysisResult" 
              @click="resetAll" 
              class="btn btn-secondary">
