<script>
import { uploadFolder, startAnalysis } from '@/api'

export default {
  name: 'FolderUpload',
  data() {
    return {
      isDragOver: false,
      uploading: false,
      uploadProgress: 0,
      uploadStatus: '',
      uploadResult: null,
      selectedFiles: [],
      analyzing: false,
      analysisStatus: '',
      analysisResult: null,
      uploadedFolderId: null, // ‰øùÂ≠ò‰∏ä‰º†ÊàêÂäüÁöÑÊñá‰ª∂Â§πID
      features: [
        {
          id: 1,
          icon: 'üìù',
          title: 'ÊëòË¶ÅËØ≠Ê≠•ÂàÜÊûê',
          description: 'Êô∫ËÉΩÊèêÂèñËÆ∫ÊñáÊëòË¶ÅÁöÑÂõõ‰∏™Ê†áÂáÜËØ≠Ê≠•ÁªìÊûÑ'
        },
        {
          id: 2,
          icon: 'üèä',
          title: 'ÂÜÖÂÆπÂàÜÂå∫ÂàíÂàÜ',
          description: 'Â∞ÜËÆ∫ÊñáÁ´†ËäÇËá™Âä®Êò†Â∞ÑÂà∞Âõõ‰∏™Ê†áÂáÜÂÜÖÂÆπÂàÜÂå∫'
        },
        {
          id: 3,
          icon: 'üìä',
          title: 'ÂõæË°®Êò†Â∞Ñ',
          description: 'Êô∫ËÉΩËØÜÂà´ËÆ∫Êñá‰∏≠ÁöÑÂõæË°®Âπ∂Êò†Â∞ÑÂà∞Áõ∏Â∫îÂÜÖÂÆπÊ≥≥ÈÅì'
        },
        {
          id: 4,
          icon: '‚ö°',
          title: 'Âπ∂ÂèëÂ§ÑÁêÜ',
          description: 'Â§öËøõÁ®ãÂπ∂Ë°åÂ§ÑÁêÜÔºåÈ´òÊïàÂàÜÊûêÂ§ßËßÑÊ®°ËÆ∫Êñá'
        },
        {
          id: 5,
          icon: 'üéØ',
          title: 'Êô∫ËÉΩÂàÜÊûê',
          description: 'Âü∫‰∫éÂ§ßËØ≠Ë®ÄÊ®°ÂûãÁöÑË∑®Â≠¶ÁßëËØ≠‰πâÁêÜËß£'
        },
        {
          id: 6,
          icon: 'üìã',
          title: 'ÁªìÊûÑÂåñËæìÂá∫',
          description: 'ÁîüÊàê9‰∏™Ê†áÂáÜÂåñÁöÑJSONÊñá‰ª∂Ôºå‰æø‰∫éÂêéÁª≠ÂàÜÊûê'
        }
      ]
    }
  },
  methods: {
    // Ëß¶ÂèëÊñá‰ª∂ÈÄâÊã©
    triggerFileInput() {
      this.$refs.fileInput.click()
    },
    
    // Â§ÑÁêÜÊñá‰ª∂ÈÄâÊã©
    handleFileSelect(event) {
      const files = Array.from(event.target.files)
      if (files.length > 0) {
        this.selectedFiles = files
        this.startUpload(files)
      }
    },
    
    // Â§ÑÁêÜÊãñÊãΩ
    handleDrop(event) {
      event.preventDefault()
      this.isDragOver = false
      
      const files = Array.from(event.dataTransfer.files)
      if (files.length > 0) {
        this.selectedFiles = files
        this.startUpload(files)
      }
    },
    
    // ÂºÄÂßã‰∏ä‰º†
    async startUpload(files) {
      this.uploading = true
      this.uploadProgress = 0
      this.uploadStatus = 'ÂáÜÂ§á‰∏ä‰º†...'
      this.uploadResult = null
      
      try {
        // ÂàõÂª∫FormData
        const formData = new FormData()
        
        // Ê∑ªÂä†ÊâÄÊúâÊñá‰ª∂Âà∞FormData
        files.forEach((file) => {
          // ‰øùÊåÅÊñá‰ª∂Âú®Êñá‰ª∂Â§π‰∏≠ÁöÑÁõ∏ÂØπË∑ØÂæÑÁªìÊûÑ
          const relativePath = file.webkitRelativePath || file.name
          formData.append('files', file, relativePath)
        })
        
        // ÂèëÈÄÅ‰∏ä‰º†ËØ∑Ê±Ç
        const response = await uploadFolder(formData, (progress) => {
          this.uploadProgress = progress
          this.uploadStatus = `‰∏ä‰º†‰∏≠... ${progress}%`
        })
        
        const result = response.data
        
        this.uploadResult = {
          success: true,
          message: 'Êñá‰ª∂Â§π‰∏ä‰º†ÊàêÂäüÔºÅ',
          folderPath: result.folder_path,
          folderId: result.folder_id,
          fileCount: result.file_count
        }
        
        // 1ÁßíÂêéÈöêËóè‰∏ä‰º†ÊàêÂäüÊèêÁ§∫
        setTimeout(() => {
          this.uploadResult = null
        }, 100000)
        
        // ‰øùÂ≠òÊñá‰ª∂Â§πIDÁî®‰∫éÂêéÁª≠ÂàÜÊûê
        this.uploadedFolderId = result.folder_id
        
      } catch (error) {
        console.error('‰∏ä‰º†ÈîôËØØ:', error)
        this.uploadResult = {
          success: false,
          message: `‰∏ä‰º†Â§±Ë¥•: ${error.message}`
        }
      } finally {
        this.uploading = false
        this.uploadProgress = 100
      }
    },
    
    // ÈáçÁΩÆ‰∏ä‰º†Áä∂ÊÄÅ
    resetUpload() {
      this.uploadResult = null
      this.selectedFiles = []
      this.uploadProgress = 0
      this.uploadStatus = ''
      this.uploadedFolderId = null
    },
    
    // ÂºÄÂßãÂàÜÊûê
    async startAnalysis() {
      if (!this.uploadedFolderId) {
        alert('ËØ∑ÂÖà‰∏ä‰º†Êñá‰ª∂Â§π')
        return
      }
      
      this.analyzing = true
      this.analysisStatus = 'Ê≠£Âú®ÂêØÂä®ÂàÜÊûêÊµÅÁ®ã...'
      this.analysisResult = null
      
      try {
        // Ë∞ÉÁî®ÂêéÁ´ØÂàÜÊûêAPI
        const response = await startAnalysis(this.uploadedFolderId)
        const result = response.data
        
        this.analysisResult = {
          success: result.success,
          message: result.message,
          analysisResult: result.analysis_result
        }
        
        if (result.success) {
          this.analysisStatus = 'ÂàÜÊûêÂÆåÊàêÔºÅ'
        } else {
          this.analysisStatus = 'ÂàÜÊûêÂ§±Ë¥•'
        }
        
      } catch (error) {
        console.error('ÂàÜÊûêÈîôËØØ:', error)
        this.analysisResult = {
          success: false,
          message: `ÂàÜÊûêÂ§±Ë¥•: ${error.message}`
        }
        this.analysisStatus = 'ÂàÜÊûêÂ§±Ë¥•'
      } finally {
        this.analyzing = false
      }
    },
    
    // ÈáçÁΩÆÊâÄÊúâÁä∂ÊÄÅ
    resetAll() {
      this.uploadResult = null
      this.analysisResult = null
      this.selectedFiles = []
      this.uploadProgress = 0
      this.uploadStatus = ''
      this.analysisStatus = ''
      this.analyzing = false
      this.uploadedFolderId = null
    }
  }
}
</script>

