import axios from 'axios'

// ÂàõÂª∫axiosÂÆû‰æã
const api = axios.create({
  baseURL: '/api', // ‰ΩøÁî®‰ª£ÁêÜË∑ØÂæÑÔºåÂú®Áîü‰∫ßÁéØÂ¢É‰∏≠ÂèØ‰ª•ÈÖçÁΩÆ‰∏∫ÂÆûÈôÖÁöÑÂêéÁ´ØÂú∞ÂùÄ
  timeout: 30000, // 0.5ÂàÜÈíüË∂ÖÊó∂ÔºåÈÄÇÂêàÊñá‰ª∂‰∏ä‰º†
  headers: {
    'Content-Type': 'application/json',
  },
})

// ËØ∑Ê±ÇÊã¶Êà™Âô®
api.interceptors.request.use(
  (config) => {
    // Âú®ÂèëÈÄÅËØ∑Ê±Ç‰πãÂâçÂÅö‰∫õ‰ªÄ‰πà
    console.log('ÂèëÈÄÅËØ∑Ê±Ç:', config.method?.toUpperCase(), config.url)
    return config
  },
  (error) => {
    // ÂØπËØ∑Ê±ÇÈîôËØØÂÅö‰∫õ‰ªÄ‰πà
    console.error('ËØ∑Ê±ÇÈîôËØØ:', error)
    return Promise.reject(error)
  }
)

// ÂìçÂ∫îÊã¶Êà™Âô®
api.interceptors.response.use(
  (response) => {
    // ÂØπÂìçÂ∫îÊï∞ÊçÆÂÅöÁÇπ‰ªÄ‰πà
    console.log('ÂìçÂ∫îÊàêÂäü:', response.status, response.config.url)
    return response
  },
  (error) => {
    // ÂØπÂìçÂ∫îÈîôËØØÂÅöÁÇπ‰ªÄ‰πà
    console.error('ÂìçÂ∫îÈîôËØØ:', error.response?.status, error.message)
    
    // Áªü‰∏ÄÈîôËØØÂ§ÑÁêÜ
    if (error.response) {
      // ÊúçÂä°Âô®ÂìçÂ∫î‰∫ÜÈîôËØØÁä∂ÊÄÅÁ†Å
      const { status, data } = error.response
      let message = 'ËØ∑Ê±ÇÂ§±Ë¥•'
      
      if (status === 400) {
        message = data.message || 'ËØ∑Ê±ÇÂèÇÊï∞ÈîôËØØ'
      } else if (status === 401) {
        message = 'Êú™ÊéàÊùÉËÆøÈóÆ'
      } else if (status === 403) {
        message = 'Á¶ÅÊ≠¢ËÆøÈóÆ'
      } else if (status === 404) {
        message = 'ËØ∑Ê±ÇÁöÑËµÑÊ∫ê‰∏çÂ≠òÂú®'
      } else if (status === 500) {
        message = 'ÊúçÂä°Âô®ÂÜÖÈÉ®ÈîôËØØ'
      } else if (status >= 500) {
        message = 'ÊúçÂä°Âô®ÈîôËØØ'
      }
      
      error.message = message
    } else if (error.request) {
      // ËØ∑Ê±ÇÂ∑≤ÁªèÂèëÂá∫Ôºå‰ΩÜÊ≤°ÊúâÊî∂Âà∞ÂìçÂ∫î
      error.message = 'ÁΩëÁªúËøûÊé•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªú'
    } else {
      // ÂÖ∂‰ªñÈîôËØØ
      error.message = error.message || 'Êú™Áü•ÈîôËØØ'
    }
    
    return Promise.reject(error)
  }
)

// Êñá‰ª∂‰∏ä‰º†‰∏ìÁî®ÊñπÊ≥ï
export const uploadFolder = (formData, onProgress) => {
  return api.post('/upload-folder/', formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
    onUploadProgress: (progressEvent) => {
      if (onProgress && progressEvent.total) {
        const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total)
        onProgress(percentCompleted)
      }
    },
  })
}

// Ëé∑Âèñ‰∏ä‰º†Áä∂ÊÄÅ
export const getUploadStatus = (folderId) => {
  return api.get(`/upload-status/${folderId}/`)
}

// ÂêØÂä®ÂàÜÊûêÊµÅÁ®ã
export const startAnalysis = (folderId) => {
  return api.post('/start-analysis/', {
    folder_id: folderId
  })
}

// Ëé∑ÂèñÂàÜÊûêÁªìÊûú
export const getAnalysisResult = (folderId) => {
  return api.get(`/analysis-result/${folderId}/`)
}

export default api
